/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-create-block-cta {
  border: 1px dashed #f00; }

.cta-container {
  position: relative;
  padding: 10px 20px; }
  .cta-container * {
    text-align: center;
    position: relative;
    z-index: 1; }
  .cta-container .cta-overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; }
